import * as React from "react"

const YouTubeEmbed = ({
    videoId,
    title = "YouTube Video",
    autoplay = false,
    startTime = 0
}) => {
    // Build URL parameters for clean viewing experience
    const params = new URLSearchParams({
        rel: '0',              // Show related videos from same channel only
        modestbranding: '1',   // Remove YouTube logo from control bar
        showinfo: '0',         // Hide video info before playback
        controls: '1',         // Show player controls
        fs: '1',               // Enable fullscreen
        iv_load_policy: '3',   // Hide video annotations
        cc_load_policy: '0',   // Hide closed captions by default
        disablekb: '0',        // Enable keyboard controls
        playsinline: '1',      // Play inline on iOS
        ...(autoplay && { autoplay: '1', mute: '1' }), // Autoplay requires mute
        ...(startTime && { start: startTime.toString() })
    })

    const embedUrl = `https://www.youtube.com/embed/${videoId}?${params.toString()}`

    return (
        <div
            style={{
                position: 'relative',
                paddingBottom: '56.25%',
                height: 0,
                overflow: 'hidden',
                maxWidth: '100%',
                marginBottom: '2rem',
                border: '1px solid #e5e7eb',
                borderRadius: '8px',
                backgroundColor: '#000'
            }}
        >
            <iframe
                style={{
                    position: 'absolute',
                    top: 0,
                    left: 0,
                    width: '100%',
                    height: '100%'
                }}
                src={embedUrl}
                title={title}
                frameBorder="0"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; fullscreen"
                allowFullScreen
                loading="lazy"
            />
        </div>
    )
}

export default YouTubeEmbed
