import * as React from "react"

const typeStyles = {
  info: {
    borderColor: "var(--color-editorial-accent)",
    label: "Note",
  },
  action: {
    borderColor: "var(--color-editorial-accent)",
    label: "Take Action",
  },
}

const Callout = ({ type = "info", children }) => {
  const style = typeStyles[type] || typeStyles.info

  return (
    <div
      className="my-6 p-4 rounded border-l-4 bg-[var(--color-editorial-light-bg)]"
      style={{ borderColor: style.borderColor }}
    >
      {style.label && (
        <p
          className="mb-2 text-[var(--color-editorial-grey)]"
          style={{ fontSize: "var(--text-xs)", textTransform: "uppercase", letterSpacing: "0.04em" }}
        >
          {style.label}
        </p>
      )}
      <div className="space-y-2">{children}</div>
    </div>
  )
}

export default Callout
